.element-choose {
    width: 10em;
    min-width: fit-content;
    height: fit-content;
    padding: 0 15px;
    text-align: center;
    background-color: var(--contrast-color);
    color: var(--font-color-dark);
    font-weight: bold;
    border-radius: 5px;
}

.element-choose:hover {
    background-color: var(--contrast-color-hover);
    cursor: pointer;
}

.element-choose:active {
    background-color: var(--contrast-color-active);
    cursor: pointer;
}

@media (max-width: 924px) and (orientation: landscape) {
    .element-choose {
        font-size: 0.8em;
    }
}

@media (max-width: 417px) and (orientation: portrait) {
    .element-choose {
        font-size: 0.8em;
        padding: 0 5px;
        min-width: none;
        width: 8em;
    }

    .element-choose p {
        width: 4em;
        white-space: pre-line;
    }
}